import { Component,OnInit } from '@angular/core';
import { FormControl, FormBuilder, FormArray } from '@angular/forms';
import { JsonCreate } from 'src/app/services/jsonCreate.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import * as moment from 'moment';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {MatChipInputEvent} from '@angular/material/chips';


import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import '@ckeditor/ckeditor5-build-classic/build/translations/ru';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';


export interface Fruit {
  name: string;
}
@Component({
  selector: 'app-dnevnik-editor',
  templateUrl: './dnevnik-editor.component.html',
  styleUrls: ['./dnevnik-editor.component.sass']
})
export class DnevnikEditorComponent implements OnInit {

  public Editor = ClassicEditor
  
  public config = {
    language: 'ru',
    toolbar: [ "redo","undo",'|','heading', '|', 'bold', 'italic', '|','link', 'bulletedList', 'numberedList','|', 'blockQuote',],


};

  text = new FormControl('')
  groups = new FormControl('')
  title = new FormControl('')

  dnevnikForm = this.fb.group({
    date: moment().format("MM-DD-YYYY"),
    text: '',
    groups: '',
    title: '',
  })

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  fruits: Fruit[] = [
    {name: 'Love'},


  ];


  
  

  constructor(private snackBar: MatSnackBar, private js: JsonCreate, private fb: FormBuilder,private route: ActivatedRoute) {
    console.log(ClassicEditor.builtinPlugins.map( plugin => plugin.pluginName ))

  }


  ngOnInit() {
    console.log(this.route)
    this.val = this.route.snapshot.queryParams.text
      
  }

  //  addButton(title: string,message: string,collection:string){
  //   this.uploudCreate(title,message,collection)
  //   this.openSnackBar()

  //  }



  upld2() {
    console.log(this.dnevnikForm)
    this.dnevnikForm.value.text = this.val
    this.js.setDataFirestore('dnevnik',this.dnevnikForm.value)
  }

  openSnackBar() {
    this.snackBar.open("Запись добавлена", ")))0)", {
      duration: 2000,
    });
  }


  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || '').trim()) {
      this.fruits.push({name: value.trim()});
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }
  }

  remove(fruit: Fruit): void {
    const index = this.fruits.indexOf(fruit);

    if (index >= 0) {
      this.fruits.splice(index, 1);
    }
  }

  val = ''
  public onChange( { editor }: ChangeEvent ) {
    const data = editor.getData();
    this.val= data
    console.log( data );
}



}
